<template>
  <section class="main">
    <input
      id="toggle-all"
      class="toggle-all"
      type="checkbox"
      v-model="isCheckAll"
    />
    <label for="toggle-all">Mark all as complete</label>
    <ul class="todo-list">
      <!-- completed: 完成的类名 -->
      <li
        :class="{ completed: item.isDone }"
        v-for="item in list"
        :key="item.id"
      >
        <div class="view">
          <input
            class="toggle"
            type="checkbox"
            @change="change(item.id)"
            :checked="item.isDone"
          />
          <label>{{ item.name }}</label>
          <!-- 删除按钮 -->
          <button class="destroy" @click="delTodos(item.id)"></button>
        </div>
        <input class="edit" value="Create a TodoMVC template" />
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  // 接受来自父组件的todoList数据
  props: {
    list: {
      type: Array,
      required: true
    }
  },
  methods: {
    // 删除
    delTodos(id) {
      //   console.log(id)
      this.$emit('delTodos', id)
    },
    //任务状态修改
    change(id) {
      this.$emit('change', id)
    }
  },
  computed: {
    isCheckAll: {
      get() {
        return this.list.every((item) => item.isDone === true)
      },
      set(value) {
        this.$emit('checkAll', value)
      }
    }
  }
}
</script>

