<template>
  <div>
    <header class="header">
      <h1>todos</h1>

      <input
        class="new-todo"
        placeholder="做点什么呢?"
        autofocus
        @keyup.enter="add"
      />
    </header>
  </div>
</template>

<script>
export default {
  methods: {
    add(e) {
      // console.log(e.target.value)
      if (e.target.value.trim() === '') return alert('请输入内容')
      this.$emit('add', e.target.value)
      e.target.value = ''
    }
  }
}
</script>

