<template>
  <div>
    <footer class="footer" v-if="this.list.length">
      <span class="todo-count"
        ><strong>{{ successNum }}</strong> 剩余任务</span
      >
      <ul class="filters">
        <li>
          <a
            href="#/"
            :class="{ selected: activeIndex === index }"
            v-for="(item, index) in actives"
            :key="item"
            @click="changeACtiveIndex(index, item)"
            >{{ item }}</a
          >
        </li>
      </ul>
      <!-- Hidden if no completed items are left ↓ -->
      <button class="clear-completed" @click="clearALL" v-if="isShow">
        清除已完成
      </button>
    </footer>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      required: true
    },
    actives: {
      type: Array,
      required: true
    },
    activeIndex: {
      type: Number,
      required: true
    }
  },
  methods: {
    // 改变激活的按钮
    changeACtiveIndex(index, name) {
      this.$emit('changeACtiveIndex', index, name)
    },
    clearALL() {
      this.$emit('clearALL')
    }
  },
  // 剩余任务
  computed: {
    successNum() {
      return this.list.filter((item) => !item.isDone).length
    },
    // 清楚所有这个按钮 如果没有完成的任务就不要显示了
    isShow() {
      return this.list.some((item) => item.isDone)
    }
  }
}
</script>

