<template>
    <section class="todoapp">
        <TodoHeader @add="add"></TodoHeader>
        <TodoMain :list="copyList" @delTodos="delTodos" @change="changeFn" @checkAll="checkAll"></TodoMain>
        <TodoFooter :actives="actives" :activeIndex="activeIndex" @changeACtiveIndex="changeACtiveIndex" :list="list" @clearALL="clearALL"></TodoFooter>
    </section>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoMain from './components/TodoMain.vue'
import TodoFooter from './components/TodoFooter.vue'
export default {
    // 挂载子组件
    components: {
        TodoHeader,
        TodoMain,
        TodoFooter
    },
    data() {
        return {
            list: JSON.parse(localStorage.getItem('todos')) || [],
            actives: ['全部', '已完成', '未完成'],
            activeIndex: 0 //底部激活
        }
    },
    methods: {
        //todo 点击X删除
        delTodos(id) {
            // console.log('来自子组件的id', id)
            this.list = this.list.filter((item) => item.id !== id)
            localStorage.setItem('todos', JSON.stringify(this.list))
        },

        //todo 修改状态
        changeFn(id) {
            const findID = this.list.find((item) => item.id === id)
            // console.log(findID)
            findID.isDone = !findID.isDone
            localStorage.setItem('todos', JSON.stringify(this.list))
        },

        // todo 修改底部激活按钮的样式
        changeACtiveIndex(index) {
            this.activeIndex = index
        },

        //todo 添加任务
        add(value) {
            // console.log(value)
            this.list.unshift({
                name: value,
                id: +new Date(),
                isDone: false
            })
            localStorage.setItem('todos', JSON.stringify(this.list))
        },

        // 删除已完成
        clearALL() {
            this.list = this.list.filter((item) => !item.isDone)
            localStorage.setItem('todos', JSON.stringify(this.list))
        },

        // 全选
        checkAll(value) {
            console.log(123)
            this.list.forEach((item) => (item.isDone = value))
        }
    },
    computed: {
        copyList() {
            if (this.activeIndex === 1) {
                return this.list.filter((item) => item.isDone === true)
            } else if (this.activeIndex === 2) {
                return this.list.filter((item) => item.isDone === false)
            } else {
                return this.list
            }
        }
    }
}
</script>

